* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* NAVBAR */
.navbar {
  background-color: #ececec;
  box-shadow: 1px 1px 5px #aaa;
  z-index: 999;
  position: sticky;
  top: 0;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: auto;
  width: 40%;
  padding: 2rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.nav li a {
  transition: all 0.33s;
}

.nav li a:hover,
.nav li a.active {
  color: #ca3636;
  cursor: pointer;
  border-radius: 10px;
}

/* HOME */
.home-main {
  background: #fff;
  /* color: #fff; */
}

.home {
  margin: auto;
  padding: 2rem;
  /* text-align: center; */
  width: 80%;
}

.home .name {
  font-family: 'Times New Roman', Times, serif;
  text-align: center;
  font-size: 3rem;
  margin-bottom: 2rem;
}

.home .details {
  font-size: 1.6rem;
  font-family: 'Times New Roman', Times, serif;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.details .intro {
}

/* ABOUT */
.about-main {
  font-size: 1.6rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url('./images/AiRobo.jpeg');
  background-repeat: no-repeat;
  background-size: 100%;
  color: #000;
}

.about {
  box-shadow: 0px 1px 5px #eee;
  background-color: #ccc;
  margin: auto;
  padding: 2rem;
  /* text-align: center; */
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.about-linkedin {
  margin: 0.5rem;
  color: inherit;
  text-decoration: underline;
}

/* PROJECTS */
.projects {
  margin: 3rem auto;
  width: 80%;
}

.project {
  margin: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.project-info {
  width: 90%;
  font-size: 1.5rem;
}

.project .details-btn {
  border-radius: 5px;
  padding: 0.4rem;
  border: none;
  color: white;
  background-color: green;
}

/* PROJECTDETAILS */
.project-details-main {
  width: 70%;
  margin: 2rem auto;
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}

.project-details {
  font-size: 2rem;
}

/* LOADING */
.Loadings {
  margin: 5rem auto;
  text-align: center;
  min-height: 65vh;
}

/* BLOG */
.blog {
  width: 70%;
  margin: 3rem auto;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.blog-post {
  padding: 1.8rem;
  border-radius: 5px;
  box-shadow: 1px 1px 5px #aaa;
}

.blog-post .title-date {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.blog-post .title-date p {
  color: #555;
  font-size: 1.2rem;
  /* font-weight: 600; */
}

.blog-desc {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 1rem;
}

.blog-content {
  font-size: 1.5rem;
}

.post-btn {
  padding: 0.4rem 1rem;
  border-radius: 5px;
  border: none;
  background-color: rgb(66, 66, 255);
  font-weight: 600;
  color: #fff;
  letter-spacing: 0.2rem;
  box-shadow: 1px 1px 5px #333;
  display: flex;
}

.post-btn:hover {
  cursor: pointer;
  box-shadow: 3px 3px 10px #aaa;
}

/* CONTACT */
.Contact {
  width: 100%;
}

.contact-form {
  margin-top: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* FOOTER */
.footer {
  margin-top: auto;
  background-color: #f5f5f5;
  font-size: 1.2rem;
  box-shadow: 0 -0.5px 1px #aaa;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 1rem;
}

.footer-main {
  margin: 2rem;
  display: flex;
  gap: 4rem;
  align-items: flex-start;
}

.footer-name {
}

.footer-links {
  display: flex;
  flex-direction: column;
  font-size: 1.5rem;
}

.footer-links .social {
  font-weight: bold;
  text-decoration: underline;
}
